:root {
  --background: 0 0% 100%;
  --foreground: 154 55% 10%;
  --card: 0 0% 100%;
  --card-foreground: 154 55% 10%;
  --muted: 100 20% 97%;
  --muted-foreground: 154 10% 40%;
  --border: 154 10% 90%;
  --input: 154 10% 90%;
  --primary: 107 36% 23%;
  --primary-foreground: 0 0% 100%;
  --secondary: 89 46% 48%;
  --secondary-foreground: 0 0% 100%;
  --accent: 0 0% 97.6%;
  --accent-foreground: 107 36% 23%;
  --destructive: 0 84% 60%;
  --destructive-foreground: 0 0% 100%;
  --ring: 89 46% 48%;
  --radius: 0.75rem;
  --shadow-sm: 0 1px 2px 0 rgb(46 78 37 / 0.05);
  --shadow: 0 4px 6px -1px rgb(46 78 37 / 0.1), 0 2px 4px -2px rgb(46 78 37 / 0.1);
  --shadow-md: 0 10px 15px -3px rgb(46 78 37 / 0.1), 0 4px 6px -4px rgb(46 78 37 / 0.1);
  --shadow-lg: 0 20px 25px -5px rgb(46 78 37 / 0.1), 0 8px 10px -6px rgb(46 78 37 / 0.1);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border-color: hsl(var(--border));
}
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  -webkit-tap-highlight-color: transparent;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:focus-visible {
  outline: 2px solid hsl(var(--ring));
  outline-offset: 2px;
}
::selection {
  background-color: hsl(var(--primary) / 0.2);
  color: hsl(var(--foreground));
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: hsl(var(--border));
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--muted-foreground) / 0.5);
}
h1, .h1 {
  font-size: 2.25rem;
  line-height: 2.5rem;
  font-weight: 700;
  letter-spacing: -0.025em;
}
h2, .h2 {
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 600;
  letter-spacing: -0.025em;
}
h3, .h3 {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 600;
}
h4, .h4 {
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 600;
}
.text-muted {
  color: hsl(var(--muted-foreground));
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  50% {
    opacity: 0.5;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes slideInFromBottom {
  from {
    transform: translateY(10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.animate-spin {
  animation: spin 1s linear infinite;
}
.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
.animate-fadeIn {
  animation: fadeIn 0.2s ease-out;
}
.animate-slideIn {
  animation: slideInFromBottom 0.2s ease-out;
}
